import { DATA_SERVER } from '../constants/server-data.js';
import Cookies from 'js-cookie';


async function getUsersMessages() {
	const token = Cookies.get('userToken');

	const response = await fetch(DATA_SERVER.MESSAGES, {
		method: 'GET',
		headers: {
			'Content-Type': 'application/json;charset=utf-8',
			'Authorization': `Bearer ${token}`
		}
	})

	if (response.ok) {
		return response.json();
	} else {
		alert('–û—à–∏–±–∫–∞ –Ω–∞ —Å–µ—Ä–≤–µ—Ä–µ'); // –î–æ–¥–µ–ª–∞—Ç—å –Ω–æ—Ä–º–∞–ª—å–Ω—É—é –æ—à–∏–±–∫—É
	}
}

export { getUsersMessages };