import { DATA_SERVER } from "../constants/server-data.js";
import { modalElements } from "../constants/ui-elements.js";
import { resetInput } from "../utils-functions.js";
import Cookies from 'js-cookie';

async function changeName(e) {
	e.preventDefault()
	const userName = modalElements.inputChangeName.value;
	const token = Cookies.get('userToken');

	const response = await fetch(DATA_SERVER.URL, {
		method: 'PATCH',
		headers: {
			'Content-Type': 'application/json;charset=utf-8',
			'Authorization': `Bearer ${token}`
		},
		body: JSON.stringify({ name: userName })
	})

	if (response.ok) {
		modalElements.textSuccsess.classList.add('show');
		resetInput(modalElements.inputChangeName);
	}
}

export { changeName };